html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  text-align: center;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 36px;
}

.product-card__info {
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 16px;
  display: flex;
}

.product-card__title {
  text-align: left;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  display: flex;
}

.product-card__review-count {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__btn {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 11px 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

